.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.cForm {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column
}
.cHeader {
  padding: 0.5rem;
  padding-bottom: 0.2rem;
  font-size: 1rem;
}
.cInput {
  outline: none;
  font-size: 1rem;
  border-radius: 3rem;
  border-style: none;
  color: white;
  padding: 0.5rem 0.5rem;
  background-color: #596375;
  
}
.cInput:focus .cInput:active {
  outline: none;
}
.cSubmit{
  margin: 1.5rem;
  padding: 1rem;
  font-size: 1.5rem;
  
  border-style: solid;
  
  font-weight: bold;
  border-radius: 2rem;
  width: 15rem;
  transition: 0.5s ease;
  background-color: transparent;
  border-color: gold;
  color: gold;
}
.cSubmit:hover .cSubmit:active {
  border-color: transparent;
  background-color: gold;
  color: #282c34;
}
.showBox {
  position: fixed;
  top: calc(50% - 15rem);
  left: calc(50% - 15rem);
  background: #596375;
  width: 100vw;
  height: 100vh;
  max-width: 30rem;
  max-height: 30rem;
  border-radius: 2rem;
}
.cImage {
  display: block;
  margin: 3rem auto 2rem auto;
  border-radius: 2rem;
  width: 20rem;
  height: 20rem;
}
.calc {
  position: fixed;
}